<template>
  <div id="fullpage" ref="fullpage">
    <div class="section">
      <One></One>
    </div>
    <div class="section">
      <Two></Two>
    </div>
    <div class="section">
      <Three></Three>
    </div>
    <div class="section">
      <Four></Four>
    </div>
    <div class="section">
      <Five></Five>
    </div>
  </div>

</template>

<script>
import fullpage from 'fullpage.js';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default {
  name: 'index',
  data() {
    return {
      helloFont: '',
    }
  },
  components: {
    liItem: () => import('@/components/liItem.vue'),
    One: () => import('@/components/index/One.vue'),
    Two: () => import('@/components/index/Two.vue'),
    Three: () => import('@/components/index/Three.vue'),
    Four: () => import('@/components/index/Four.vue'),
    Five: () => import('@/components/index/Five.vue'),
  },
  created() {
    this.getTimeState()
  },
  mounted() {
    this.show()
    this.$message.info("现在是" +
        this.dayjs().format("YYYY-MM-DD HH:mm:ss" + this.helloFont)
    )

    this.$nextTick(() => {


      AOS.init({
        // 全局配置:
        disable: false, // 禁用。可取值: 'phone'、'tablet'、'mobile'、布尔值、表达式或函数
        startEvent: 'DOMContentLoaded', // AOS 应初始化在 document 上绑定的事件名
        initClassName: 'aos-init', // 初始化后使用的 class 类
        animatedClassName: 'aos-animate', // 动画 class
        useClassNames: false, // 值为 true，将添加滚动 `data-aos` 内容为 class
        disableMutationObserver: false, // disables automatic mutations' detections (高级)
        debounceDelay: 50, // 调整窗口大小时使用的 (高级)
        throttleDelay: 99, // 滚动页面时使用的延迟 (高级)
        // 通过 'data-aos-*' 属性在每个元素上重写配置:
        offset: 0, // 从原始触发点的偏移量 (px)
        delay: 0, // 取值范围 0-3000, 每多 1 加 50ms
        duration: 400, // 取值范围 0-3000, 每多 1 加 50ms
        easing: 'ease', // AOS 动画默认 easing
        once: false, // 向下滑动时，动画只发生一次
        mirror: true, // 滚动到元素上方时是否应设置动画
        anchorPlacement: 'top-bottom', // 定义与窗口有关的元素的哪个位置应触发动画
      });

    })
    new fullpage('#fullpage', {
      sectionsColor: ['#21252b', '#21252b', '#111111', '#21252b', '#21252b'],
      navigation: true,
      scrollBar: true,
      scrollOverflow: false
    });
  },
  methods: {

    //   问候语
    getTimeState() {
      const hours = this.dayjs().format("HH")
      // 判断当前时间段
      if (hours >= 0 && hours <= 4) {
        this.helloFont = `🌙 又熬夜了吗，小心猝死哦。`;
      } else if (hours > 4 && hours <= 6) {
        this.helloFont = `😮 刚睡醒还是熬通宵了？注意安全`;
      } else if (hours > 6 && hours <= 11) {
        this.helloFont = `😝 早上好~`;
      } else if (hours > 11 && hours <= 13) {
        this.helloFont = `🥰 中午好~`;
      } else if (hours > 13 && hours <= 17) {
        this.helloFont = `😙 下午好~`;
      } else if (hours > 17 && hours <= 22) {
        this.helloFont = `🥱 晚上好~`;
      } else if (hours > 22 && hours <= 24) {
        this.helloFont = `😴 已经很晚了，早点睡吧~`;
      }
    },
    //   烟花特效
    show() {
      function clickEffect() {
        let balls = [];
        let longPressed = false;
        let longPress;
        let multiplier = 0;
        let width, height;
        let origin;
        let normal;
        let ctx;
        const colours = ["#ffc0cb", "#45a2df", "#d7698e", "#c1dbc7", "#810ca8"];
        const canvas = document.createElement("canvas");
        document.body.appendChild(canvas);
        canvas.setAttribute("style", "width: 100%; height: 100%; top: 0; left: 0; z-index: 99999; position: fixed; pointer-events: none;");
        const pointer = document.createElement("span");
        pointer.classList.add("pointer");
        document.body.appendChild(pointer);

        if (canvas.getContext && window.addEventListener) {
          ctx = canvas.getContext("2d");
          updateSize();
          window.addEventListener('resize', updateSize, false);
          loop();
          window.addEventListener("mousedown", function (e) {
            pushBalls(randBetween(10, 20), e.clientX, e.clientY);
            document.body.classList.add("is-pressed");
            longPress = setTimeout(function () {
              document.body.classList.add("is-longpress");
              longPressed = true;
            }, 500);
          }, false);
          window.addEventListener("mouseup", function (e) {
            clearInterval(longPress);
            if (longPressed == true) {
              document.body.classList.remove("is-longpress");
              pushBalls(randBetween(50 + Math.ceil(multiplier), 100 + Math.ceil(multiplier)), e.clientX, e.clientY);
              longPressed = false;
            }
            document.body.classList.remove("is-pressed");
          }, false);
          window.addEventListener("mousemove", function (e) {
            let x = e.clientX;
            let y = e.clientY;
            pointer.style.top = y + "px";
            pointer.style.left = x + "px";
          }, false);
        } else {
          console.log("canvas or addEventListener is unsupported!");
        }


        function updateSize() {
          canvas.width = window.innerWidth * 2;
          canvas.height = window.innerHeight * 2;
          canvas.style.width = window.innerWidth + 'px';
          canvas.style.height = window.innerHeight + 'px';
          ctx.scale(2, 2);
          width = (canvas.width = window.innerWidth);
          height = (canvas.height = window.innerHeight);
          origin = {
            x: width / 2,
            y: height / 2
          };
          normal = {
            x: width / 2,
            y: height / 2
          };
        }

        class Ball {
          constructor(x = origin.x, y = origin.y) {
            this.x = x;
            this.y = y;
            this.angle = Math.PI * 2 * Math.random();
            if (longPressed == true) {
              this.multiplier = randBetween(14 + multiplier, 15 + multiplier);
            } else {
              this.multiplier = randBetween(6, 12);
            }
            this.vx = (this.multiplier + Math.random() * 0.5) * Math.cos(this.angle);
            this.vy = (this.multiplier + Math.random() * 0.5) * Math.sin(this.angle);
            this.r = randBetween(8, 12) + 3 * Math.random();
            this.color = colours[Math.floor(Math.random() * colours.length)];
          }

          update() {
            this.x += this.vx - normal.x;
            this.y += this.vy - normal.y;
            normal.x = -2 / window.innerWidth * Math.sin(this.angle);
            normal.y = -2 / window.innerHeight * Math.cos(this.angle);
            this.r -= 0.3;
            this.vx *= 0.9;
            this.vy *= 0.9;
          }
        }

        function pushBalls(count = 1, x = origin.x, y = origin.y) {
          for (let i = 0; i < count; i++) {
            balls.push(new Ball(x, y));
          }
        }

        function randBetween(min, max) {
          return Math.floor(Math.random() * max) + min;
        }

        function loop() {
          ctx.fillStyle = "rgba(255, 255, 255, 0)";
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          for (let i = 0; i < balls.length; i++) {
            let b = balls[i];
            if (b.r < 0) continue;
            ctx.fillStyle = b.color;
            ctx.beginPath();
            ctx.arc(b.x, b.y, b.r, 0, Math.PI * 2, false);
            ctx.fill();
            b.update();
          }
          if (longPressed == true) {
            multiplier += 0.2;
          } else if (!longPressed && multiplier >= 0) {
            multiplier -= 0.4;
          }
          removeBall();
          requestAnimationFrame(loop);
        }

        function removeBall() {
          for (let i = 0; i < balls.length; i++) {
            let b = balls[i];
            if (b.x + b.r < 0 || b.x - b.r > width || b.y + b.r < 0 || b.y - b.r > height || b.r < 0) {
              balls.splice(i, 1);
            }
          }
        }
      }

      clickEffect();//调用特效函数
    }
  }
}
</script>

<style lang="less" scoped>
@font-face {
  font-family: 'FrederickatheGreat';
  src: url('@/assets/font/FrederickatheGreat-Regular.ttf') format('truetype');
}

#fullpage {


  .four {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

    #particles {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      z-index: 0;
    }


    .fourContainer {
      z-index: 11 !important;
      width: 60%;
      min-width: 350px;
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-direction: column;

      .fourTitle {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;

        img {
          width: 25px;
          margin-right: 10px;
          margin-top: 5px;
        }

        h1 {
          color: #FF8080;
        }
      }
    }
  }

  @media screen and (max-width: 600px) {
    .two {
      grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
    }


    .twoTitle {
      font-size: 30px;
      font-weight: bold;
    }

    .twoFontItem {

    }

    .vanish {
      display: none;
    }
  }
}
</style>
